@import (reference) "../bootstrap/variables.less";
@import (reference) "../../components/bootstrap/less/mixins.less";
@import (reference) "../mixins.less";
@import (reference) "../utility.less";
@import (reference) "../bootstrap/type.less";

.spot {

    strong {
        font-size: 16px;
    }

    &__trompet {
    }

    &__heading {
    }

    &__content {
        a {
            .box-hover(@brand-primary, @brand-primary, 2px);
        }
    }
}

.spot--hero-fluid {
    color: #fff;
    position: relative;
    width: 100vw;
    height: 70vh;


    .spot {
        &__media {
            height: 80vh;

            video {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 70vh;
                object-fit: cover;
                z-index: -1;
            }
        }

        &__trumpet {
            .trumpet();
            text-align: center;
        }

        &__heading {
            .fluid-property(margin-bottom, 35, 55);
            font-weight: 300;
        }

        &__content {
            position: absolute;
            z-index: 1;
            width: 70vw;
            max-width: 1540px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        &__call-to-action {
            text-align: center;
        }
    }
}

.spot--content-left {
    position: relative;
    color: #fff;

    .container {
        position: absolute;
        top: calc(~"50% - 40px");
        transform: translateY(-50%);
        left: 0;
        z-index: 1;
    }

    .spot {
        &__media {
            height: 550px;
        }

        &__trumpet {
            .trumpet();
        }

        &__heading {
            .fluid-property(margin-bottom, 17, 27);
        }

        &__content {
            padding: 0 15px;
        }

        &__call-to-action {
            text-align: center;
        }
    }
}

.spot--content-right {
    position: relative;
    color: #fff;

    .container {
        position: absolute;
        top: calc(~"50% + 30px");
        transform: translateY(-50%);
        left: 0;
        z-index: 1;
    }

    .spot {
        &__media {
            height: 550px;
        }

        &__trumpet {
            .trumpet();
        }

        &__heading {
            .fluid-property(margin-bottom, 17, 27);
        }

        &__content {
            padding: 0 15px;
        }

        &__call-to-action {
            text-align: center;
        }
    }
}

.spot--hero-fluid.spot--content-left {
    position: relative;
    color: #fff;
    text-align: left;

    .container {
        position: absolute;
        top: calc(~"50% - 15px");
        transform: translateY(-50%);
        left: 0;
        z-index: 1;
    }

    .spot {
        &__media {
            height: 550px;
        }

        &__trumpet {
            text-align: left;
            .trumpet();
            .fluid-property(margin-bottom, 5, 10);
        }

        &__heading {
            .fluid-property(margin-bottom, 17, 27);
        }

        &__content {
            position: relative;
            width: auto;
            max-width: none;
            top: auto;
            left: auto;
            transform: translate(0,0);
            padding: 0 15px;
        }

        &__call-to-action {
            text-align: center;
        }
    }
}

.spot--hero-fluid.spot--content-right {
    position: relative;
    color: #fff;
    text-align: left;

    .container {
        position: absolute;
        top: calc(~"50% - 15px");
        transform: translateY(-50%);
        left: 0;
        z-index: 1;
    }

    .spot {
        &__media {
            height: 550px;
        }

        &__trumpet {
            text-align: left;
            .trumpet();
            .fluid-property(margin-bottom, 5, 10);
        }

        &__heading {
            .fluid-property(margin-bottom, 17, 27);
        }

        &__content {
            position: relative;
            width: auto;
            max-width: none;
            top: auto;
            left: auto;
            transform: translate(0,0);
            padding: 0 15px;
        }

        &__call-to-action {
            text-align: center;
        }
    }
}
